import React from 'react';
import {connect} from 'react-redux';
import {Table} from 'reactstrap';
import {ToastsContainer, ToastsStore, ToastsContainerPosition} from 'react-toasts';
import {getLoanRequests, approveLoan} from "actions/LoanRequests";
import IntlMessages from 'util/IntlMessages';
import Spinner from 'components/Spinner';


class loanRequests extends React.Component {


    componentDidMount(){
      this.props.getLoanRequests();
    }

    componentWillReceiveProps(newProps){
        if(newProps.requestLoans.approveLoanSuccessMessage.StatusCode === 200){
            ToastsStore.success(newProps.requestLoans.approveLoanSuccessMessage.Message);
        }
    }

    approveLoan(index, id){
        let data = {
            request_id: id
        }
        this.props.approveLoan(data);
    }

    render(){
        return (
            <div className={'loanContainer'}>
{/*
                {this.props.requestLoans.approveLoanSuccessMessage !== {} ? <div className={'successCustom'}>{this.props.requestLoans.approveLoanSuccessMessage.Message}</div> : ''}
*/}
                <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT} color={'white'}/>

                <div className={'row'}>
                   <div className={'col-12'}>
                       <div className={'jr-card'}>
                           <div className="jr-card-header d-flex align-items-center">
                               <h3 className="mb-0"><IntlMessages id="faq.category"/></h3>
                               <div className="ml-3">
                                <span className="text-white badge badge-success">
                                    <IntlMessages id="faq.aboutLender"/></span>
                               </div>
                           </div>
                           {this.props.requestLoans.loanRequests ?
                               <div className="table-responsive-material">
                                   <Table>
                                       <thead>
                                          <tr>
                                              <th className="border-top-0">S/N</th>
                                              <th className="border-top-0">Account Balance (N)</th>
                                              <th className="border-top-0">Recharge Card (N)</th>
                                              <th className="border-top-0">Merit Amount (N)</th>
                                              <th className="border-top-0">Credit Rating (%)</th>
                                              <th className="border-top-0">Loan History Rating (%)</th>
                                              <th className="border-top-0">Approve</th>
                                              <th className="border-top-0">Paid</th>
                                              <th className="border-top-0">Platform</th>
                                              <th className="border-top-0">Actions</th>
                                          </tr>
                                       </thead>
                                       <tbody>
                                       {this.props.requestLoans.loanRequests.map((item, index) => {
                                           let status;
                                           if(item.is_approved === "1")
                                               status = "Approved";
                                           else if(item.is_approved === "-1")
                                               status = "Disapproved";
                                           else
                                               status = "Pending";

                                           return (
                                               <tr key={index}>
                                                   <td>{index + 1}</td>
                                                   <td>{item.discounted_sum_of_account_balance}</td>
                                                   <td>{item.discounted_sum_of_recharge_cards}</td>
                                                   <td>{item.total_merit_amount}</td>
                                                   <td>{item.credit_rating}</td>
                                                   <td>{item.loan_history_rating}</td>
                                                   <td>{status}</td>
                                                   <td>{item.is_fully_paid === "1" ? 'Paid' : 'Not Paid'}</td>
                                                   <td>{item.platform}</td>
                                                   <td>
                                                       <div className={'actionBtn'}>
                                                           <div>
                                                           <i className="zmdi zmdi-check cursorChange" onClick={() => this.approveLoan(index, item.id)}></i>
                                                           </div>
                                                           <div>
                                                               <i className="zmdi zmdi-close"></i>
                                                           </div>
                                                       </div>
                                                   </td>
                                               </tr>
                                           )
                                       })

                                       }
                                       </tbody>
                                   </Table>
                               </div> : ''

                           }
                       </div>
                   </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    requestLoans: state.loanRequests
})


export default connect(mapStateToProps, {getLoanRequests, approveLoan})(loanRequests);


